import './App.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import React, {useState, useEffect} from "react";
import {CanvasJSChart} from 'canvasjs-react-charts'
// import useCollapse from 'react-collapsed';
import {Slider} from 'rsuite';
import Switch from "react-switch";

// function Collapsible() {
//   const { getCollapseProps, getToggleProps, isExpanded } = useCollapse();
// return (
//   <div className="collapsible">
//       <div className="header" {...getToggleProps()}>
//           {isExpanded ? 'Collapse' : 'Expand'}
//       </div>
//       <div {...getCollapseProps()}>
//           <div className="content">
//               Now you can see the hidden content. <br/><br/>
//               Click again to hide...
//           </div>
//       </div>
//   </div>
//   );
// }

function App() {
  const [oldStyle, setOldStyle] = useState(false)
  const [monthList, setMonthList] = useState(["Jan", "Feb", "Mar", "Apr", "May", "Jun", "Jul", "Aug", "Sep", "Oct", "Nov", "Dec"])
  const [purchaseOrderPm, setPurchaseOrderPm] = useState(0)
  const [increaseInPurchaseOrder, setIncreaseInPurchaseOrder] = useState(0)
  const [expectedProfitPercentage, setExpectedProfitPercentageChange] = useState(0)
  const [increaseInExpectedProfitPercentage, setIncreaseInExpectedProfitPercentage] = useState(0)
  const [overHeadCost, setOverHeadCost] = useState(0)
  const [increaseInOverHeadCost, setIncreaseInOverHeadCost] = useState(0)
  const [amountToRecover, setAmountToRecover] = useState(0)
  const [calculation, setCalculation] = useState([])
  const [calculationSummary, setCalculationSummary] = useState('')
  const [graphData, setGraphData] = useState({})

  useEffect( () => {
    handleResetToDefaults()
  }, [])
  

  const handleOldStyle = (evt) => {
    setOldStyle(evt)
  }
  const handleResetToDefaults = () => {
    setMonthList(["Jan", "Feb", "Mar", "Apr", "May", "Jun", "Jul", "Aug", "Sep", "Oct", "Nov", "Dec"])
    setPurchaseOrderPm(350000)
    setIncreaseInPurchaseOrder(25)
    setExpectedProfitPercentageChange(8)
    setIncreaseInExpectedProfitPercentage(1.5)
    setOverHeadCost(25000)
    setIncreaseInOverHeadCost(1.5)
    setAmountToRecover(2500000)
    setCalculation([])
    setCalculationSummary('')
    setGraphData({})
  }
  const handlePurchaseOrderChange = (evt) => {
    setPurchaseOrderPm(Number(evt.target.value))
  }

  const handleIncreaseInPurchaseOrderChange = (evt) => {
    setIncreaseInPurchaseOrder(Number(evt.target.value))
  }

  const handleExpectedProfitPercentageChange = (evt) => {
    setExpectedProfitPercentageChange(Number(evt.target.value))
  }

  const handleIncreaseInProfitPercentageChange = (evt) => {
    setIncreaseInExpectedProfitPercentage(Number(evt.target.value))
  }

  const handleOverHeadCostChange = (evt) => {
    setOverHeadCost(Number(evt.target.value))
  }

  const handleIncreaseInOverHeadCostChange = (evt) => {
    setIncreaseInOverHeadCost(Number(evt.target.value))
  }

  const handleAmountToRecoverChange = (evt) => {
    setAmountToRecover(Number(evt.target.value))
  }

  const handleCalculate = () => {
    console.log()
    var calculation = []
    var year = 0
    var purchase_amount = purchaseOrderPm
    var profit_percentage = expectedProfitPercentage
    var over_head_cost = overHeadCost
    var cumulative_profit = 0.0
    var isTargetAchived = false
    var graphPurchaseAmount = []
    var graphOverHeadCost = []
    var graphCumulativeProfit = []
    var graphRecoverAmount = []
    while (cumulative_profit <= amountToRecover)
    {
      var month = 1
      year += 1
      var profit_before_oh = Number(((purchase_amount * profit_percentage)/100.0).toFixed(2))
      var net_profit = Number((profit_before_oh - over_head_cost).toFixed(2))
      for (month = 1; month <= 12; month++)
      {
        var record = calculation.slice();
        
        cumulative_profit += net_profit
        var delta_amount = amountToRecover - cumulative_profit
        if (delta_amount < 0 && isTargetAchived === false)
        {
          setCalculationSummary("You'd earn " + amountToRecover + " in " + year + " year(s) and " + month + " month(s)")
          isTargetAchived = true
        }
        record.push({"year": new Date().getFullYear() + year,
                     "month": month,
                     "purchase_amount": purchase_amount,
                     "profit_percentage": profit_percentage,
                     "profit_before_oh": profit_before_oh,
                     "over_head_cost": over_head_cost,
                     "net_profit": net_profit,
                     "cumulative_profit": cumulative_profit,
                     "delta_amount": delta_amount
                    });

        calculation = record
        if (month === 12)
        {

          var gra = graphRecoverAmount.slice()
          gra.push({y: amountToRecover, label: (new Date().getFullYear()) + year})
          graphRecoverAmount = gra

          var gpa = graphPurchaseAmount.slice()
          gpa.push({y: purchase_amount, label: (new Date().getFullYear()) + year})
          graphPurchaseAmount = gpa

          var goc = graphOverHeadCost.slice()
          goc.push({y: over_head_cost, label: (new Date().getFullYear()) + year})
          graphOverHeadCost = goc

          var gcp = graphCumulativeProfit.slice()
          gcp.push({y: cumulative_profit, label: (new Date().getFullYear()) + year})
          graphCumulativeProfit = gcp
        }
      }
      purchase_amount = Number((purchase_amount * ( 1 + increaseInPurchaseOrder/100.0)).toFixed(2))
      profit_percentage = Number((profit_percentage * ( 1 + increaseInExpectedProfitPercentage/100.0)).toFixed(2))
      over_head_cost = Number((over_head_cost * ( 1 + increaseInOverHeadCost/100.0)).toFixed(2))
    }
    setCalculation(calculation)
    // console.log(calculation)


    setGraphData(
      {
        backgroundColor: "#f0f0f0",
        animationEnabled: true,	
        title:{text: "Business Prediction"},
        axisY : {title: "Money in Rupees"},
        toolTip: {shared: true},
        data: [
                {
                  type: "spline",
                  name: "Amount To Recover",
                  showInLegend: true,
                  dataPoints: graphRecoverAmount
                },
                {
                  type: "spline",
                  name: "Purchase Amount",
                  showInLegend: true,
                  dataPoints: graphPurchaseAmount
                },
                // {
                //   type: "spline",
                //   name: "Over Head Cost",
                //   showInLegend: true,
                //   dataPoints: graphOverHeadCost
                // },
                {
                  type: "spline",
                  name: "Cumulative Profit",
                  showInLegend: true,
                  dataPoints: graphCumulativeProfit
                }
              ]
        }
    )
  }

  const handlePurchaseOrderSliderChange = (evt) => {
    // console.log(evt)
    setPurchaseOrderPm(Number(evt))
  }

  const handleIncreaseInPurchaseOrderSliderChange = (evt) => {
    setIncreaseInPurchaseOrder(Number(evt))
  }

  const handleExpectedProfitPercentageSliderChange = (evt) => {
    setExpectedProfitPercentageChange(Number(evt))
  }

  const handleIncreaseInProfitPercentageSliderChange = (evt) => {
    setIncreaseInExpectedProfitPercentage(Number(evt))
  }

  const handleOverHeadCostSliderChange = (evt) => {
    setOverHeadCost(Number(evt))
  }

  const handleIncreaseInOverHeadCostSliderChange = (evt) => {
    setIncreaseInOverHeadCost(Number(evt))
  }

  const handleAmountToRecoverSliderChange = (evt) => {
    setAmountToRecover(Number(evt))
    // handleCalculate()
  }
  
  return (
    
    <div className="App">
      <div className="title">
        <div className='row'>
          <div className='col-md-9'>SK Biz</div>
          <div className='enableOldStyleLabel col-md-3'>
            <Switch
              onChange={(evt) => handleOldStyle(evt)}
              checked={oldStyle}
              className="react-switch"
            />Old Style
          </div>
        </div>
      </div>
      <div className='metadata'>  
        Metadata
        <div className="container">
          <div className="row metadataRow purchaseOrder">
              {oldStyle ?
                <div className="metadataLabel col-md-3"> <label>Purchase Order </label></div>
              :
                <div className="metadataLabel col-md-3"> <label>Purchase Order </label> <label className='metadataLabelSmall'> ({purchaseOrderPm}) </label></div>
              }
              <div className="col-md-3">
                {oldStyle ?
                  <input className='metadataMoney' onChange={(evt => handlePurchaseOrderChange(evt))} value = {purchaseOrderPm} ></input>
                :
                  <Slider className='metadataMoneySlider' onChange={(evt) => handlePurchaseOrderSliderChange(evt)} value={purchaseOrderPm} min={200000} max={500000} graduated step={50000} progress/>
                }
              </div>
              {oldStyle ?
                <div className="metadataLabel col-md-2"> <label> % increase pa </label></div>
              :
              <div className="metadataLabel col-md-2"> <label> % increase pa </label> <label className='metadataLabelSmall'> ({increaseInPurchaseOrder}) </label> </div>
              }
              <div className="col-md-3">
                {oldStyle ?
                  <input className='metadataMoney' onChange={(evt => handleIncreaseInPurchaseOrderChange(evt))} value = {increaseInPurchaseOrder} ></input>
                :
                  <Slider className='metadataMoneySlider' onChange={(evt) => handleIncreaseInPurchaseOrderSliderChange(evt)} value={increaseInPurchaseOrder} min={20} max={40} graduated step={5} progress/>
                }
              </div>
          </div>

          <div className="row metadataRow">
              {oldStyle ?
                <div className="metadataLabel col-md-3"> <label>Profit Percentage </label></div>
              :
                <div className="metadataLabel col-md-3"> <label>Profit Percentage </label> <label className='metadataLabelSmall'> ({expectedProfitPercentage}) </label> </div>
              }
              <div className="col-md-3">
                {oldStyle ?
                  <input className='metadataMoney' onChange={(evt => handleExpectedProfitPercentageChange(evt))} value = {expectedProfitPercentage} ></input>
                :
                  <Slider className='metadataMoneySlider' onChange={(evt) => handleExpectedProfitPercentageSliderChange(evt)} value={expectedProfitPercentage} min={7} max={12} graduated step={0.5} progress/>
                }
              </div>
              {oldStyle ?
                <div className="metadataLabel col-md-2"> <label> % increase pa </label></div>
              :
                <div className="metadataLabel col-md-2"> <label> % increase pa </label> <label className='metadataLabelSmall'> ({increaseInExpectedProfitPercentage}) </label> </div>
              }
              <div className="col-md-3">
                {oldStyle ?
                  <input className='metadataMoney' onChange={(evt => handleIncreaseInProfitPercentageChange(evt))} value = {increaseInExpectedProfitPercentage} ></input>
                :
                  <Slider className='metadataMoneySlider' onChange={(evt) => handleIncreaseInProfitPercentageSliderChange(evt)} value={increaseInExpectedProfitPercentage} min={1} max={5} graduated step={0.5} progress/>
                }
              </div>
          </div>

          <div className="row metadataRow overHead">
            {oldStyle ?
              <div className="metadataLabel col-md-3"> <label>Overhead Cost</label></div>
              :
              <div className="metadataLabel col-md-3"> <label>Overhead Cost</label> <label className='metadataLabelSmall'> ({overHeadCost}) </label> </div>
            }
              <div className="col-md-3">
                {oldStyle ?
                  <input className='metadataMoney' onChange={(evt => handleOverHeadCostChange(evt))} value = {overHeadCost} ></input>
                :
                  <Slider className='metadataMoneySlider' onChange={(evt) => handleOverHeadCostSliderChange(evt)} value={overHeadCost} min={15000} max={50000} graduated step={2500} progress/>
                }
              </div>
              {oldStyle ?
                <div className="metadataLabel col-md-2"> <label> % increase pa </label></div>
              :
              <div className="metadataLabel col-md-2"> <label> % increase pa </label> <label className='metadataLabelSmall'> ({increaseInOverHeadCost}) </label> </div>
              }
              <div className="col-md-3">
                {oldStyle ?
                  <input className='metadataMoney' onChange={(evt => handleIncreaseInOverHeadCostChange(evt))} value = {increaseInOverHeadCost} ></input>
                :
                  <Slider className='metadataMoneySlider' onChange={(evt) => handleIncreaseInOverHeadCostSliderChange(evt)} value={increaseInOverHeadCost} min={1} max={5} graduated step={0.5} progress/>
                }
              </div>
          </div>

          <div className="row metadataRow">
            {oldStyle ?
              <div className="metadataLabel col-md-3"> <label>Amount to Recover  </label></div>
            :
              <div className="metadataLabel col-md-3"> <label>Amount to Recover  </label> <label className='metadataLabelSmall'> ({amountToRecover}) </label> </div>
            }
            <div className="col-md-3">
              {oldStyle ?
                <input className='metadataMoney' onChange={(evt => handleAmountToRecoverChange(evt))} value = {amountToRecover} ></input>
              :
                <Slider className='metadataMoneySlider' onChange={(evt) => handleAmountToRecoverSliderChange(evt)} value={amountToRecover} min={2000000} max={4000000} graduated step={500000} progress/>
              }
            </div>
          </div>
            
          <div className="row metadataRow controlButton">
            <div className='col-md-2'>
            </div>
            <div className='col-md-4'>
              <button className = 'metadataButton' onClick = {() => handleCalculate()}> Calculate </button>
            </div>
            <div className='col-md-4'>
              <button className = 'metadataButton' onClick = {() => handleResetToDefaults()}> Reset to Defaults</button>
            </div>
          </div>
        </div>
      </div>
      
      {
        Object.keys(graphData).length === 0 ? <></> : <div className='graph'> <CanvasJSChart options = {graphData} /> </div>
      }
      {/* <div className='graph'> <CanvasJSChart options = {graphData} /> </div> */}
      <div className='results'>
        Summary
        <div>
          <div className='resultsSummary'> {calculationSummary} </div>
          <table className='resultsTable'>
            <tbody>
            <tr className='resultsTableHeader'>
              <th>Year</th>
              <th>Month</th>
              <th>Purchase Amount</th>
              <th>Profit Percentage</th>
              <th>Profit Before OH</th>
              <th>Overhead Cost</th>
              <th>Net Profit</th>
              <th>Cumulative Profit</th>
              <th>Delta Amount</th>
            </tr>
            {
              calculation.map((row) =>  { 
                // console.log(row)
                return(<tr className = {row["delta_amount"] <0 ? 'resultsTableRows targetAchieved' : row["year"] %2 === 1 ? 'resultsTableRows oddYear' : 'resultsTableRows'} key={row}>
                                                <td> {row["year"]} </td>
                                                {/* <td> {new Date().setMonth(1).toLocaleString('en-US', {month: 'short'})} </td> */}
                                                {/* <td> {row["month"]} </td> */}
                                                <td> { monthList[Number(row["month"]) -1]}</td>
                                                <td> {row["purchase_amount"].toFixed(2)} </td>
                                                <td> {row["profit_percentage"].toFixed(2)} </td>
                                                <td> {row["profit_before_oh"].toFixed(2)} </td>
                                                <td> {row["over_head_cost"].toFixed(2)} </td>
                                                <td> {row["net_profit"].toFixed(2)} </td>
                                                <td> {row["cumulative_profit"].toFixed(2)} </td>
                                                <td> {row["delta_amount"].toFixed(2)} </td>
                                              </tr>)}
                              )
            }
          </tbody>
          </table>
        </div>
      </div>
      
    </div>
  );
}

export default App;
